@import url(https://fonts.googleapis.com/css2?family=Roboto|Oswald|Titillium+Web);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@600|Lato:ital,wght@1,300);
.pro_container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 20px;

  position: relative;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  z-index: 0;
}

.project {
  position: relative;
  border: 1px solid black;
  width: 350px;
  background-color: #1a2238;
  margin: 30px 40px;
}
.heading {
  position: relative;
  padding: 5px;
  cursor: pointer;
  font-size: 1.3rem;
  font-family: "Roboto", sans-serif;
  color: #d48166;
  background-color: #051622;
}
.heading:hover {
  background-color: black;
}
.heading::after {
  content: "▼";
  color: greenyellow;
  position: absolute;
  right: 20px;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 0.8rem;
  opacity: 0.6;
  -webkit-animation: move 2s infinite;
  animation: move 2s infinite;
}
@-webkit-keyframes move {
  0% {
    top: 40%;
  }
  100% {
    top: 60%;
  }
}
@keyframes move {
  0% {
    top: 40%;
  }
  100% {
    top: 60%;
  }
}
.desc {
  line-height: 1.5rem;
  padding: 5px 10px 10px 10px;
  font-size: 1rem;
  display: none;
  color: #deb992;
  font-family: "Titillium Web", sans-serif;
}
.desc a {
  display: block;
  text-decoration: none;
  color: rgb(247, 247, 255);

  padding: 5px;
  letter-spacing: 2px;
  text-align: center;
  font-size: 1rem;
  margin: 5px;
  background-color: rgb(78, 51, 78);
  transition: letter-spacing 400ms;
}
.desc a:hover {
  letter-spacing: 0px;
}
.heading1::after {
  content: "▲";
  -webkit-animation: move1 2s infinite;
          animation: move1 2s infinite;
}
@-webkit-keyframes move1 {
  0% {
    top: 60%;
  }
  100% {
    top: 40%;
  }
}
@keyframes move1 {
  0% {
    top: 60%;
  }
  100% {
    top: 40%;
  }
}
.techstak {
  margin-top: 10px;
  color: green;
}
.techstak > span {
  font-size: 1.1rem;
  font-family: "Oswald", sans-serif;
  color: orange;
}
@media (max-width: 600px) {
  .pro_container {
    padding: 30px;
  }

  .project {
    margin: 10px;
    position: relative;

    background-color: black;
  }
}

* {
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
  scroll-behavior: smooth;
}
#skills,
#projects {
  max-width: 1500px;

  margin: 100px auto;
}
#projects {
  margin: 100px auto 50px auto;
}
#skills > h1,
#projects > h1 {
  background-color: black;
  color: rgb(10, 102, 240);
  text-align: center;
  letter-spacing: 4px;
  font-size: 2.1rem;
  transition: letter-spacing 300ms;
  position: relative;
}
#skills > h1:hover,
#projects > h1:hover {
  letter-spacing: 0px;
}
#skills > h1::before,
#projects > h1::before {
  content: "";
  background: #4e54c8;
  background: linear-gradient(to right, #8f94fb, #4e54c8);
  height: 2px;
  width: 80%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 100%;
  left: 50%;
  transition: all 800ms;
}

#skills > h1:hover::before,
#projects > h1:hover::before {
  width: 0%;
}
#skills > h1::after,
#projects > h1::after {
  content: "";
  background: linear-gradient(to right, #1565c0, #b92b27);
  height: 2px;
  width: 0%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 100%;
  left: 50%;
  transition: all 800ms;
  transition-delay: 0s;
}
#skills > h1:hover::after,
#projects > h1:hover::after {
  width: 80%;
}

.skills_container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: black;
  position: relative;
  background-color: black;
  z-index: 0;
  padding: 20px;
}
.skills_container > div {
  width: 350px;
  padding: 10px 0px 20px 0px;
  margin: 30px 40px 30px 40px;
  position: relative;

  background-color: black;
}
.skills_container > div > div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 10px;
}
.skills_container > div::before,
.project::before {
  content: "";
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  z-index: -1;

  -webkit-animation: bgdc 8s infinite linear;

          animation: bgdc 8s infinite linear;
}
@-webkit-keyframes bgdc {
  0% {
    background: linear-gradient(to right, #f64f59, #c471ed, #12c2e9);
  }
  25% {
    background: linear-gradient(to right, #c06c84, #6c5b7b, #355c7d);
  }
  50% {
    background: linear-gradient(to right, #1565c0, #b92b27);
  }
  75% {
    background: linear-gradient(to right, #ff0000, #fff200, #1e9600);
  }
  100% {
    background: linear-gradient(to right, #ec2f4b, #009fff);
  }
}
@keyframes bgdc {
  0% {
    background: linear-gradient(to right, #f64f59, #c471ed, #12c2e9);
  }
  25% {
    background: linear-gradient(to right, #c06c84, #6c5b7b, #355c7d);
  }
  50% {
    background: linear-gradient(to right, #1565c0, #b92b27);
  }
  75% {
    background: linear-gradient(to right, #ff0000, #fff200, #1e9600);
  }
  100% {
    background: linear-gradient(to right, #ec2f4b, #009fff);
  }
}
.skills > p {
  padding: 3px 20px 3px 20px;
  font-size: 1rem;
  margin: 10px;
  position: relative;
  z-index: 1;
}

.skills > p::before {
  content: "";
  background: linear-gradient(to right, #ec2f4b, #009fff);
  height: 2px;
  width: 100%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 100%;
  left: 50%;
  transition: all 800ms;
}
.skills > p:nth-child(even) {
  color: red;
}
.skills > p:nth-child(odd) {
  color: yellow;
}
.skills > p:hover::before {
  width: 0%;
}
.skills > p::after {
  content: "";
  background: linear-gradient(to right, #1565c0, #b92b27);
  height: 2px;
  width: 0%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 100%;
  left: 50%;
  transition: all 800ms;
  transition-delay: 0s;
}
.skills > p:hover::after {
  width: 100%;
}

.skills_container > div > h2 {
  text-align: center;
  font-size: 1.5rem;
  position: relative;
  color: greenyellow;
}
.skills_container > div > h2::before {
  content: "";
  background: #fc466b;
  background: linear-gradient(to right, #3f5efb, #fc466b);

  height: 2px;
  width: 70%;
  position: absolute;

  -webkit-transform: translate(-50%, -50%);

          transform: translate(-50%, -50%);
  top: 100%;
  left: 50%;
  transition: all 800ms;
}
.skills_container > div > h2:hover::before {
  width: 0%;
}
.skills_container > div > h2::after {
  content: "";
  background: #355c7d;
  background: linear-gradient(to right, #c06c84, #6c5b7b, #355c7d);

  height: 2px;
  width: 0%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 100%;
  left: 50%;
  transition: all 800ms;
}
.skills_container > div > h2:hover::after {
  width: 70%;
}
@media (max-width: 600px) {
  #skills {
    margin: 50px auto;
  }
  .skills_container > div {
    padding: 10px 0px 20px 0px;
