nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;

  max-width: 700px;
  margin: auto;
}
.navbar {
  background-color: rgb(9, 33, 48);
  width: 100%;
  position: fixed;
  padding: 10px;

  z-index: 5;
  top: 0%;
}
nav > a {
  text-decoration: none;
  color: gray;
  font-size: 1.3rem;

  padding: 0px 15px;

  position: relative;
}
nav > a::before {
  content: "";
  width: 0%;
  height: 2px;
  background-color: yellow;
  position: absolute;
  bottom: 0%;
  left: 0%;
  transition: all 0.3s;
}
nav > a:hover {
